import * as React from 'react';
import clsx from 'clsx';
import { withStyles, WithStyles } from '@material-ui/core';
import scrollIntoView from 'scroll-into-view';
import Stepper from '@material-ui/core/Stepper';
import Step from '@material-ui/core/Step';
import StepLabel from '@material-ui/core/StepLabel';
import StepConnector from '@material-ui/core/StepConnector';
import { StepIconProps } from '@material-ui/core/StepIcon';
import FiberManualRecordOutlinedIcon from '@material-ui/icons/FiberManualRecordOutlined';
import FiberManualRecordIcon from '@material-ui/icons/FiberManualRecord';
import VisibilityDetector from 'react-visibility-detector';
import  get from 'lodash/get';
import keysIn from 'lodash/keysIn';
import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import ListItemText from '@material-ui/core/ListItemText';
import ExpandLess from '@material-ui/icons/ExpandLess';
import ExpandMore from '@material-ui/icons/ExpandMore';
import Collapse from '@material-ui/core/Collapse';
import Style from './style';
import { BoardOfDirectors, Resources, Values } from '../../constants/resources';
import { connect } from 'react-redux';
import { RootState } from '../../reducers';
import Loadable from '@loadable/component';
import Grid from '@material-ui/core/Grid';
const Card = Loadable(()=>import('../../components/Card')) ;
const DesktopCard = Loadable(()=>import('../../components/DesktopCard')) ;
const ImageContent = Loadable(()=>import('../../components/ImageContent')) ;
const Footer = Loadable(() => import('../../components/Footer'));

interface Props extends WithStyles<typeof Style> {
	[name: string]: any;
	isDesktopView: boolean;
}

interface State {
	step: string;
	stepping: boolean;
	selected:number;
}
const steps = [
	{ name: 'Overview', id: 'overview' },
	{ name: 'Founder', id: 'founders' },
	{ name: 'Vision', id: 'mission' },
	{ name: 'Values', id: 'values' }
];
const QontoConnector = withStyles({
	alternativeLabel: {
		left: 'calc(-50% + 16px)',
		right: 'calc(50% + 16px)',
		top: 8
	},
	active: {
		'& $line': {
			borderColor: '#979797'
		}
	},
	completed: {
		'& $line': {
			borderColor: '#979797'
		}
	},
	line: {
		borderColor: '#979797',
		borderTopWidth: 1,
		borderRadius: 1
	}
})(StepConnector);

/**
 * About page.
 * @param {any} props - Properties
 * @returns {React.Component} component.
 */
class About extends React.PureComponent<Props, State> {
	constructor(props: Props) {
		super(props);
		this.state = {
			step: 'overview',
			stepping: false,
			selected:-1
		};
	}

	componentDidMount() {
		// scrollIntoView(document.getElementById('home') as any);
	}

	takeToLink = (id) => {
		scrollIntoView(document.getElementById(id) as any, { align: { top: 0.09, left: 0 } });
		this.setState({ stepping: true, step: id });
	};

	isVisible = ({ visible, directionY }, step) => {
		const { step: activeStep, stepping } = this.state;
		if (!stepping) {
			if (visible && activeStep !== step && directionY !== 'down') {
				this.setState({ step });
			} else if (!visible) {
				if (directionY === 'down') {
					const currentStep = steps.findIndex((item) => item.id === activeStep) || 0;
					const stepName = get(steps[currentStep - 1], 'id', 'overview');
					this.setState({ step: stepName });
				}
			}
		} else {
			this.setState({ stepping: false });
		}
	};

	QontoStepIcon = (props: StepIconProps) => {
		const { classes } = this.props;
		const { active } = props;
		return (
			<div>
				{active ? (
					<FiberManualRecordIcon className={classes.smallFont} />
				) : (
						<span>
							<FiberManualRecordOutlinedIcon className={classes.smallFont} />
						</span>
					)}
			</div>
		);
	};

	getActiveStep = () => {
		const { step } = this.state;
		return steps.findIndex((item) => item.id === step);
	};

	handleSelect = (index) => {
		const {selected} = this.state;
		if(selected === index){
			this.setState({selected:-1})
		}else{
			this.setState({selected:index})
		}
	}
	/**
	 * Render method
	 * @returns {JSX.Element} Jsx.
	 */
	public render() {
		const { classes, isDesktopView } = this.props;
		const {selected} = this.state;
		return (
			<>
				{isDesktopView ?
					<div className={classes.content}>
						<DesktopCard name="About Us" image="/images/about-us-desktop.png" />
						<div className={classes.block}>
							<p
								className={clsx(
									classes.marginTop_8,
									classes.section,
									classes.smallFont
								)}
							>
								{Resources.AboutUsPara_1}
								<br />
								<br />
								{Resources.AboutUsPara_2}
								<br />
								<br />
								{Resources.AboutUsPara_3}
							</p>
						</div>
						<ImageContent content={Resources.AboutUsFounderContent} heading="Our Founder" name="Mr. Radhakishan Damani" image="/images/Founders/RKDamani.png" padding="80px 32px 64px 32px" />
						<div className={classes.bodContainer}>
							<div className={classes.bodTitle}>
								<span></span>
								<FiberManualRecordIcon className={classes.dotStart}/>Board Of Directors
								<FiberManualRecordIcon className={classes.dotEnd}/>
							</div>
							<div className={classes.bodgrid}>
								<Grid container wrap="wrap" direction="row" spacing={10}>
								{BoardOfDirectors.map((director) => (
									<Grid item sm={6} md={4} xs={12}>
										<DesktopCard
											name={director.name}
											designation={director.designation}
											description={director.description}
											image={director.image}
											small
										/>
									</Grid>
								))}
							</Grid>
							</div>
						</div>
						<div className={classes.missionContainer}>
							<DesktopCard image="/images/mission-desktop.png" />
							<div className={classes.missionContent}>
								<div className={classes.missionBg}>
									<img src="/images/mission-logo.png" alt="logo" className={classes.logo}/>
									<span className={classes.missionTitle}>Our Vision</span>
									<p
										className={classes.missionText}
									>
										{Resources.AboutUsVisionContent}
									</p>
								</div>
							</div>
						</div>
						<div className={classes.bodContainer}>
							<div className={classes.bodTitle}>
								<span></span>
								<FiberManualRecordIcon className={classes.dotStart}/>Our Values
								<FiberManualRecordIcon className={classes.dotEnd}/>
							</div>
							<div className={classes.valuesContent}>
								<Grid container wrap="nowrap" direction="row" spacing={2} justify="center">
								{Values.map((item) => (
									<Grid item sm={4} md={3} xs={12} key={item.title}>
										<div className={classes.valueCard}>
											<div className={classes.valueTitle}><span id="line"></span><p className={classes.titleFirst}><p>{item.title}</p></p></div>
											<FiberManualRecordIcon className={classes.valueDot}/>
											
												{keysIn(item.content).map((c_item)=>
												<div className={classes.valueItem}><span><span className={classes.bold}>{c_item}</span> : {get(item.content,c_item,'')}</span></div>
												)}
											
										</div>
									</Grid>
								))}
							</Grid>
							</div>
						</div>
					</div> :
					<div className={classes.content}>
						<span id="overview" />
						<Card name="" designation="" image="/images/about-us.png" smallCard />
						<VisibilityDetector onVisibilityChange={(e) => this.isVisible(e, 'overview')}>
							<div className={classes.block}>
								<span className={classes.largerFont}>About Us</span>
								<p
									className={clsx(
										classes.marginTop_8,
										classes.section,
										classes.smallFont
									)}
								>
									{Resources.AboutUsPara_1}
									<br />
									<br />
									{Resources.AboutUsPara_2}
									<br />
									<br />
									{Resources.AboutUsPara_3}
								</p>
							</div>
						</VisibilityDetector>
						<span id="founders" />
						<VisibilityDetector onVisibilityChange={(e) => this.isVisible(e, 'founders')}>
							<div className={clsx(classes.marginTop_40, classes.block)}>
								<span className={classes.largerFont}>Founder</span>
							</div>
							<Card name="" designation="" image="/images/Founders/RKDamani.png" />
							<div className={classes.block}>
								<p
									className={clsx(
										classes.marginTop_8,
										classes.section,
										classes.smallFont
									)}
								>
									{Resources.AboutUsFounderContent}
								</p>
							</div>
							<div
								className={clsx(
									classes.marginTop_40,
									classes.block,
									classes.largerFont
								)}
							>
								Board of Directors
						</div>
							<div id="cards">
								{BoardOfDirectors.map((director) => (
									<Card
										name={director.name}
										designation={director.designation}
										description={director.description}
										image={director.image}
										key={director.name}
									/>
								))}
							</div>
						</VisibilityDetector>
						<span id="mission" />
						<div className={classes.marginTop_40}>
							<Card name="" designation="" image="/images/Vision.png" smallCard />
						</div>
						<VisibilityDetector onVisibilityChange={(e) => this.isVisible(e, 'mission')}>
							<div className={clsx(classes.marginTop_8, classes.block)}>
								<span className={classes.largerFont}>Our Vision</span>
								<p
									className={clsx(
										classes.marginTop_8,
										classes.section,
										classes.smallFont
									)}
								>
									{Resources.AboutUsVisionContent}
								</p>
							</div>
						</VisibilityDetector>
						<span id="values" />
						<div className={classes.marginTop_40}>
							<Card name="" designation="" image="/images/pledge.png" smallCard />
						</div>
						<VisibilityDetector onVisibilityChange={(e) => this.isVisible(e, 'values')}>
							<div className={clsx(classes.marginTop_8, classes.block)}>
								<span className={classes.largerFont}>Our Values</span>
								<div className={clsx(classes.section, classes.smallFont)}>
									<List
										component="nav"
										aria-labelledby="nested-list-subheader"
										className={classes.list}
									>
										{Values.map((item, index) => (
											<div key={item.title} onClick={()=>this.handleSelect(index)}>
												<ListItem button className={classes.listButton}>
													<ListItemText><p className={classes.listTitle}><p style={{margin:0}}>{item.title}</p></p></ListItemText>
													{selected === index ? <ExpandLess /> : <ExpandMore />}
												</ListItem>
												<Collapse in={selected === index} timeout="auto" unmountOnExit>
													<div>
														{keysIn(item.content).map((c_item) =>
															<div className={classes.valueItem} key={c_item}><span><span className={classes.bold}>{c_item}</span> : {get(item.content, c_item, '')}</span></div>
														)}
													</div>
												</Collapse>
											</div>
										))}
									</List>
								</div>
							</div>
						</VisibilityDetector>
					</div>
				}
				{isDesktopView && <Footer />}
				<div className={classes.stepperBlock}>
					<Stepper
						alternativeLabel
						activeStep={this.getActiveStep()}
						connector={<QontoConnector />}
						orientation="horizontal"
						classes={{ root: classes.stepperRoot }}
					>
						{steps.map((item) => (
							<Step key={item.id}>
								<StepLabel
									onClick={() => this.takeToLink(item.id)}
									StepIconComponent={(props) => this.QontoStepIcon(props)}
									classes={{
										labelContainer: classes.stepperLabel,
										label: classes.alternativeLabel
									}}
								>
									{item.name}
								</StepLabel>
							</Step>
						))}
					</Stepper>
				</div>
			</>
		);
	}
}

const mapStateToProps = (state: RootState) => ({
	isDesktopView: state.screenReducer.isDesktopView
});

export default withStyles(Style, { name: 'DmartCorpFreshAbout' })(connect(mapStateToProps)(About));
